export default {
	data() {
		return {
			InputBottom: 0,
			modalName: null,
			modalName1: null,
		};
	},
	methods: {
		InputFocus(e) {
			this.InputBottom = e.detail.height
		},
		InputBlur(e) {
			this.InputBottom = 0
		},
		goMore(url) {
			uni.navigateTo({
				url: url
			});
		},
		showModal(e) {
			this.modalName = e.currentTarget.dataset.target
		},
		hideModal(e) {
			this.modalName = null
		},
		showModal1(e) {
			this.modalName1 = e.currentTarget.dataset.target
		},
		hideModal1(e) {
			this.modalName1 = null
		},
	}
}
